<template>
	<div class="note">
		<span class="green strong">{{increaseUnitCount}}</span>{{measureName}}
		<span class="red strong">{{decreaseUnitCount}}</span>{{measureName}}

		<br /><br /> {{measureName}}
		<span v-if="provinceSameRate < 0"></span>
		<span v-else-if="provinceSameRate > 0"></span>
		<span v-else></span>

		<span class="strong" v-bind:class="{red:provinceSameRate < 0, green:provinceSameRate > 0, orange:provinceSameRate == 0}">
			<number-format format="small-decimal" :numValue=provinceSameRate></number-format> 
		</span> % <span class="orange strong">{{provinceRank}}</span> 
		<template v-if="provinceSameRankRise > 0">
			<span class="green strong">{{provinceSameRankRise}}</span>
		</template>

		<template v-else-if="provinceSameRankRise < 0">
			<span class="red strong">{{provinceSameRankRise * -1}}</span>
		</template>

		<template v-else>
			
		</template>
	</div>
</template>

<script>
	import numberFormat from 'components/numberformat/number-format.vue';
	import axios from "utils/axios";
	import EventBus from '../eventBus';
	export default {
		data() {
			return {
				queryKey:"saleQty",
				increaseUnitCount: 0, //
				decreaseUnitCount: 0, //
				provinceSameRate: 0, //
				provinceRank: 0, //
				provinceSameRankRise: 0 //
			}
		},
		computed: {
			measureName: function() {
				var temp = {
					saleQty: "",
					saleAmt: "",
					avePrice: ""
				};
				return temp[this.queryKey];
			}
		},
		methods: {
			getData: function() {
				var self = this;
				self.showLoading();
				axios.get('/overviewnation/getInfoBoxData', {
						params: {
							queryKey: self.queryKey
						}
					})
					.then(function(resp) {
						var result = resp.data.result;
						
						self.increaseUnitCount = result.increaseUnitCount;
						self.decreaseUnitCount = result.decreaseUnitCount;
						self.provinceSameRate = result.provinceSameRate;
						self.provinceRank = result.provinceRank;
						self.provinceSameRankRise = result.provinceSameRankRise;
						
						self.closeLoading();
					})
					.catch(function(err) {
						self.closeLoading();
						console.log(err);
					})
			}
		},
		created(){
			var self = this;
			EventBus.$on("measureChanged",function(measureKey){
				self.queryKey = measureKey;
				self.getData();
			})
		},
		mounted() {
			this.getData();
		},
		components: {
			numberFormat
		}
	}
</script>

<style lang="scss" scoped>
	@import '../../../styles/variables.scss';
	.note {
		background-color: rgb(36, 62, 136);
		margin-left: 0.1rem;
		font-size: 0.14rem;
		line-height: 0.2rem;
		padding: 0.15rem;
		color: #fff;
		.strong {
			font-size: 0.2rem;
			font-weight: bold;
			margin: 0 0.05rem 0 0.01rem;
		}
		.red {
			color: $red;
		}
		.green {
			color: $green;
		}
		.orange {
			color: $orange;
		}
	}
</style>


